import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import Citylist from '@/components/Citylist'
// import Category from '@/components/Category'
// //1.
// import HomePage from '../components/HomePage.vue'
import CateShop from '../components/CateShop.vue'
// //1.
import ShopCar from '../components/ShopCar.vue'
// //1.
import MyPage from '../components/MyPage.vue'
//
import payment from '../components/payment.vue'
// ---------------
import Shop from '../components/shop/Index.vue'
// import SCategory from '../components/shop/Category.vue'
import SContent from '../components/shop/Content.vue'
import Content from '../components/shop/Content.vue'
// ---------------
/*  */
import Menu from '../components/menu/Index.vue'
import MContent from '../components/menu/Content.vue'  // 
// 
import Site from '../components/Site/Site.vue'
// 
import address from '../components/address.vue' 
Vue.use(Router)
export default new Router({
  // #  
  routes: [
    {
      path: '/',
      name: 'home',
      component: Homepage
      //
      // '/' ,  '/sub'
      // redirect:"/sub"
    },
    {
      path: '/citylist',
      name: 'city',
      component: Citylist
    },
    {
      path: '/CateShop',
      component: CateShop
    },
    {
      path: '/ShopCar',
      component: ShopCar
    },
    {
      path: '/MyPage',
      component: MyPage
    },
    {
      path: '/Content',
      component: Content
    },
    {
      path: '/address',
      component: address
    },
    {
      path: '/payment',
      component: payment
    },
    {path:'/shop', component:Shop,
   children: [
     { path: '/', redirect:'/shop/0' },
     // { path: ':category', component:SCategory },
     { path: ':category/:listname/:contentId', component:SContent }
   ]
  },
  {path:'/menu', component:Menu,
   children: [
     { path: 'recommend/:id', component:MContent, name:"Recommend" }
   ]
  },
  {
      path: '/site',
      component: Site,
      meta: {
        notKeepAlive: false
      }
    },
  ]
})



