import Vue from 'vue';
import Vuex from 'vuex';
import local from './local.js'

Vue.use(Vuex);

//
//state()
 // getters()
// actions()
// mutations()

// vueComponent() ->actions -> mutations -> state ->vueComponent()

//vueComponent() -> getters -> state

let store =new Vuex.Store({
    //1.state
    state:{
    	city:"",
    }
    ,

    //2.getters
    getters:{
    	//statestate
    	getCityFn(state){
           return state.city;
    	}
    },
    //3.actions
    //api
    actions:{
    	//
    	//{commit,state}
    	//statestate
    	//commitmutations
    	//name
    	setCityName({commit,state},name){
           //
           //mutations
           commit("setCity",name);
    	}
    },

    //4.mutations
    mutations:{
    	//statestate
    	//name
    	setCity(state,name){
    		state.city=name;
    	}
    }
});

export default {
    store,
    // 
     // 
  userInfo: {},
  sites: [],
  // 
  selectCity: '',
  // 
  selectSite: '',
  // 
  location: {},
  local
}


