<template>
    <div class="wrap">
    <my-header title="">
      <!-- strong my-header'left'-->
        <strong class="left_img" slot="left">
         <router-link class="city" to="citylist"><span class="city">{{city}}</span><img src="../assets/city_type.png" height="18" width="31"></router-link>
          
        </strong>
        <!-- strong my-header'right'-->
        <strong class="right_img" slot="right">
          <img   src="../assets/sousuo.png" height="32" width="40">
          </strong>
    </my-header>
    <div class="swiper">
           <!-- 03. -->
   <mt-swipe :auto="4000">
    <mt-swipe-item><img src="../assets/food_one.png" alt=""></mt-swipe-item>
    <mt-swipe-item><img src="../assets/food_two.png" alt=""></mt-swipe-item>
    <mt-swipe-item><img src="../assets/food_three.png" alt=""></mt-swipe-item>
    <mt-swipe-item><img src="../assets/food_four.png" alt=""></mt-swipe-item>
   </mt-swipe>
  </div>
  <my-menu></my-menu>
  <my-shop></my-shop>
  <my-nav></my-nav>
  </div>
  
</template>

<script>
//00.Vue
import Vue from 'vue';
//01.mint-ui
//js
import { Swipe, SwipeItem } from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

//02.mint-ui  css
import 'mint-ui/lib/style.css'
//axios
import axios from 'axios'; 
//1.
import Header from './Header.vue';
import Nav from './Nav.vue';
import MenuSwiper from './MenuSwiper.vue';
import ShopList from './ShopList.vue'
import {mapGetters,mapActions} from 'vuex';

export default {
  name: 'HelloWorld',
  data () {
    return {
       // city:''
       //---------------------
       //vuexstate
       // city:this.$store.state.city
       //---------------------
changeArr:[]
    }
  },
  computed:{
     city:function(){
     	 // return this.$store.state.city       //getters

      //vuexgettersstate   //getters
      return this.$store.getters.getCityFn;

     }
  },
  computed:mapGetters({
    city :'getCityList'
  }),
   //---------------------
  methods:{
    //()
    //
    ajaxFn:function(){
     var  that=this
      //axios
      axios.get("/student").then(function(res){
        console.log(res);
         that.changeArr=res.data.student
      });
    },
      fn:function(){
      	//vuexstate
      	// this.$store.state.city=""
  //----------------------------
        //vuexactionsstate
        //(actionsmutations)
         this.$store.dispatch("setCityName","");
  //----------------------------
      	// console.log(this.$store.state.city);
      }

  	},     
  created:function(){
  	//vuex
  	//this.$storevuex
  	console.log(this.$store);
    // console.log(this.$store.state.city);

  },
  //
  components:{
    'my-nav':Nav,
    "my-header":Header,
    "my-menu":MenuSwiper,
    "my-shop":ShopList
  }

}
</script>


<style lang='stylus' scoped>
@import '../assets/common/stylus/common.styl'
.wrap
  background #e3e0e0
  .header
    position fixed
    top 0
    left 0
    z-index 10
  .swiper
    margin-top pxtorem(85px)
    img
      width 100%
  .mint-swipe
    height pxtorem(400px)
    width 100%
  .left_img
    img
     width pxtorem(36px)
     height pxtorem(20px) 
 .right_img
    img
     width pxtorem(60px)
     height pxtorem(38px)
     padding-top pxtorem(19px)
  .city
    color white
    font-weight bold
    padding-right pxtorem(5px)
</style>



